html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-count {
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
}

.card__price-content {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all 1s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--0 .stars__star:nth-child(n), .stars--1 .stars__star:nth-child(n+2), .stars--2 .stars__star:nth-child(n+3), .stars--3 .stars__star:nth-child(n+4), .stars--4 .stars__star:nth-child(n+5) {
